import {
  Anchor,
  Button,
  Flex,
  PagePaddingX,
  Text,
  useScroll,
} from '@fx/app-front-end-components';
import type { FC, RefObject } from 'react';
import { useMemo, useRef } from 'react';
import { useRouter } from 'next/router';
import classNames from 'classnames';
import // EnvelopeClosedIcon,
// InstagramLogoIcon
'@radix-ui/react-icons';
import styles from './Header.module.scss';

const HEADER_OPACITY_DELAY_RATIO = 0.5;
const HEADER_OPACITY = 0.95;

const HEADER_ITEMS = [
  // {
  //   href: '/',
  //   render: () => (
  //     <Flex className={styles.title}>
  //       <img
  //         alt="affector.io logo"
  //         className={styles.logo}
  //         src="/media/icons/favicon.ico"
  //       />
  //       <Text useHeaderFont>FFECTOR</Text>
  //     </Flex>
  //   ),
  //   useActiveStatus: true,
  // },
  {
    href: '/',
    title: 'AFFECTOR',
    useActiveStatus: true,
  },
  // {
  //   href: '/about',
  //   title: 'About',
  // },
  // {
  //   href: '/showcase',
  //   title: 'Showcase',
  // },
  {
    href: '/contact-us',
    title: 'Contact',
    useActiveStatus: true,
  },
  {
    key: 'socials',
    href: '',
    position: 'right',
    // render: () => (
    //   <Flex className={styles.socials}>
    //     <Anchor href="mailto:info@affector.io" useSpanInline>
    //       <Button isWrapper>
    //         <EnvelopeClosedIcon />
    //       </Button>
    //     </Anchor>
    //     {/* <Anchor
    //       href="https://www.instagram.com/affector.io/"
    //       opensInNewTab
    //       useSpanInline
    //     >
    //       <Button isWrapper>
    //         <InstagramLogoIcon />
    //       </Button>
    //     </Anchor> */}
    //   </Flex>
    // ),
    render: () => null,
    useActiveStatus: true,
  },
];

export interface HeaderProps {
  backgroundAlwaysVisible?: boolean;
  scrollRef?: RefObject<HTMLDivElement>;
  heroContentRef?: RefObject<HTMLDivElement>;
}

export const Header: FC<HeaderProps> = ({
  backgroundAlwaysVisible = false,
  scrollRef,
  heroContentRef,
}) => {
  const { asPath } = useRouter();

  const headerContentRef = useRef<HTMLDivElement>(null);

  const { scrollTop } = useScroll({
    scrollContainerElementRef: scrollRef,
  });

  const headerStyle = useMemo(() => {
    const heroContentScrollTop = Math.max(
      (heroContentRef?.current?.getBoundingClientRect().top || 0) -
        (headerContentRef?.current?.offsetHeight || 0),
      0
    );

    const progressToHero = Math.max(
      0,
      Math.min(scrollTop / (heroContentScrollTop || 1), 1)
    );

    const opacityWhenUsingFadeIn =
      progressToHero < HEADER_OPACITY_DELAY_RATIO
        ? 0
        : ((progressToHero - HEADER_OPACITY_DELAY_RATIO) /
            (1 - HEADER_OPACITY_DELAY_RATIO)) *
          HEADER_OPACITY;

    return {
      '--header-background-opacity': backgroundAlwaysVisible
        ? 1
        : opacityWhenUsingFadeIn,
    } as React.CSSProperties;
  }, [scrollTop, backgroundAlwaysVisible, heroContentRef]);

  return (
    <Flex className={styles.header} style={headerStyle}>
      <Flex className={styles.headerContentsWrapper} ref={headerContentRef}>
        <div className={styles.headerBackground} />
        <PagePaddingX className={styles.headerContents}>
          <Flex className={styles.links}>
            {HEADER_ITEMS.map(
              ({
                key,
                title,
                href,
                render,
                useActiveStatus = true,
                position,
              }) => {
                if (position === 'right') {
                  return [];
                }

                const child =
                  render?.() ||
                  (title ? <Text useHeaderFont>{title}</Text> : null) ||
                  null;

                return href ? (
                  <Anchor
                    className={classNames(styles.headerAnchor, {
                      [styles.active]:
                        useActiveStatus &&
                        (href.length > 1
                          ? asPath.startsWith(href)
                          : href === asPath),
                    })}
                    href={href}
                    key={key || href + title}
                  >
                    <Button isWrapper>{child}</Button>
                  </Anchor>
                ) : (
                  child
                );
              }
            )}
          </Flex>
          <Flex className={styles.links}>
            {HEADER_ITEMS.flatMap(
              ({
                key,
                title,
                href,
                render,
                useActiveStatus = true,
                position,
              }) => {
                if (position !== 'right') {
                  return [];
                }

                const child =
                  render?.() ||
                  (title ? <Text useHeaderFont>{title}</Text> : null) ||
                  null;

                return href ? (
                  <Anchor
                    className={classNames(styles.headerAnchor, {
                      [styles.active]:
                        useActiveStatus &&
                        (href.length > 1
                          ? asPath.startsWith(href)
                          : href === asPath),
                    })}
                    href={href}
                    key={key || href + title}
                  >
                    <Button isWrapper>{child}</Button>
                  </Anchor>
                ) : (
                  child
                );
              }
            )}
          </Flex>
        </PagePaddingX>
      </Flex>
    </Flex>
  );
};
